.basicInfo {
    padding: 5px;
    .contentPart {
        padding: 10px;
        margin: auto;
        height: 65vh;
        overflow-y: auto;
        overflow-x: hidden;
        width: 100%;
    }
    .contentPart::-webkit-scrollbar{
        width: 0.3em;
      margin-left: 10px;
      }
      
      .contentPart::-webkit-scrollbar-track {
        box-shadow: inset 0 0 6px rgb(255, 255, 255);
        outline: 0.2px solid rgba(183, 186, 189, 0.331);
        border-radius: 5%;
      }
      
      .contentPart::-webkit-scrollbar-thumb {
        background-color: rgb(209, 204, 204);
        outline: 0.1px solid rgba(183, 186, 189, 0.257);
        border-radius: 5%;
      }

    hr {
        border-top: 2px solid #E8E9E9;
    }

    .btn {
        column-gap: 17px;
    }

    .rowWidth {
        // width: 100%;
    }

    .rowWidth1 {
        width: 220px ;
    }
    .AddressWidth {
        width: 540px;
    }

    .btnWidth {
        width: 160px;
    }

    .propertyLabel {
        color: #090909;
    }
    .basicForm {
        background: #FAFAFA;
        border-radius: 16px;
    }
    .basicForm2 {
        // background: #FAFAFA;
        border-radius: 16px;
    }
    
    .iconbtn {
        padding: 0;
        margin-right: 50px;
        width: 18px;
        height: 18px;
        color: #935ed6;
  
        .circleAdd {
          width: 18px;
          height: 18px;
          color: #935ed6;
        }
      }
      .iconbtn:hover {
        padding: 0;
        margin-right: 50px;
        width: 18px;
        height: 18px;
        color: #391765;
  
        .circleAdd {
          width: 18px;
          height: 18px;
          color: #391765;
        }
      }
      .table{
        width:100% ;
      }
}